<template>
  <div class="content">
    <section class="item">
      <div class="line" v-for="(item,index) in abnormal" :key="index">
        <div class="titleLine">
          <p>{{item.num}}（{{item.type}}{{item.sex!=1 ? '女':'男'}}）</p>
          <div class="right">
            <p v-if="currentType == 1">超时{{item.abnormalTime}}分钟</p>
            <p v-else>超额{{item.abnormalMoney}}元</p>
            <img src="@/assets/clock.png" />
          </div>
        </div>
        <div class="remarkInfo">{{item.remarkInfo}}</div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentType: null,
      abnormal:[]
    };
  },
  mounted() {
    this.getCurrentPageType();
  },
  methods: {
    getCurrentPageType() {
      this.currentType = this.$route.query.id;
      if(this.currentType == '1'){
        this.abnormal = JSON.parse(JSON.stringify(this.getRespone.abnormalTime));
      } else {
        this.abnormal = JSON.parse(JSON.stringify(this.getRespone.abnormalMoney));
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.item {
  padding: 0.5rem;
  .line {
    border: 0.05rem solid $F5;
    border-radius: 0.5rem;
    padding: 0.5rem;
    margin: 0.5rem 0.5rem;
    p {
      @include sc(0.9rem, $fColor);
    }
    .titleLine {
      padding: 0.5rem;
      @include fr(space-between);
      p {
        font-weight: bold;
      }
      .right {
        @include fr(center);
        p {
          @include sc(0.8rem, $line);
        }
      }
      img {
        @include wh(1.5rem, 1.5rem);
        margin-left: 0.3rem;
      }
    }
    .remarkInfo {
      margin-bottom: 0.8rem;
      padding: 0.5rem;
      border-top: 0.02rem solid $F5;
      text-align: left;
      @include sc(0.8rem, $fColor);
    }
  }
}
</style>